main .main {
  border-radius: 15px;
  background: white;
  margin: 15px;
}
@media (min-width: 71.25em) {
  main .main {
    display: flex;
    flex-direction: row;
    width: 70%;
    margin: 15px auto;
  }
}
main .main__image img {
  object-fit: cover;
  max-width: 100%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
@media (min-width: 71.25em) {
  main .main__image img {
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
    border-top-right-radius: 0;
    margin-right: 20px;
  }
}
@media (min-width: 71.25em) {
  main .main__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
main .main__heading {
  font-size: 12px;
  font-weight: 700;
  margin: 10px;
  color: var(--veryDarkGrayishBlue);
  text-align: left;
}
@media (min-width: 71.25em) {
  main .main__heading {
    margin: 0 10px 20px 20px;
    line-height: 1.6;
    text-align-last: left;
  }
}
main .main__paragraph {
  font-size: 15px;
  font-weight: 400;
  padding: 10px;
  text-align: left;
  color: var(--DeasaturatedDarkBlue);
}
@media (min-width: 71.25em) {
  main .main__paragraph {
    margin-left: 20px;
    line-height: 1.6;
    text-align-last: left;
    white-space: pre-line;
    padding: 0;
  }
}
main .main__creator {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}
@media (min-width: 71.25em) {
  main .main__creator {
    padding: 20px 20px 0 20px;
  }
}
main .main__creator--avatar {
  position: relative;
  margin: 10px;
}
main .main__creator--avatar img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
}
main .main__creator--name {
  font-weight: 300;
  color: var(--veryDarkGrayishBlue);
  font-size: 13px;
  padding: 10px;
  font-weight: 600;
  flex-grow: 2;
}
main .main__creator--name span {
  padding: 0;
  font-size: 12px;
  opacity: 0.8;
  font-weight: 400;
}
main .main__creator--share {
  margin: 10px;
  text-align: center;
  padding: 3px;
}
@media (min-width: 71.25em) {
  main .main__creator--share {
    position: static;
  }
}
main .main__creator--share button {
  border: none;
  background: var(--lightGrayishBlue);
  border-radius: 50%;
  width: 35px;
  height: 35px;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  top: 20px;
  bottom: 0;
  right: 20px;
  vertical-align: middle;
}
@media (min-width: 71.25em) {
  main .main__creator--share button {
    top: 32px;
    margin-right: 20px;
  }
}
main .main__creator--share button img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
main .main__creator--popup {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 20px;
  position: absolute;
  visibility: hidden;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background: var(--veryDarkGrayishBlue);
}
@media (min-width: 71.25em) {
  main .main__creator--popup {
    border-radius: 15px;
    height: 64px;
    left: 588px;
    top: -56px;
    width: 30%;
    justify-content: space-evenly;
  }
  main .main__creator--popup::after {
    content: "";
    position: absolute;
    width: 1.6rem;
    height: 1.6rem;
    bottom: -0.8rem;
    left: 50%;
    transform: rotate(45deg);
    background-color: var(--veryDarkGrayishBlue);
  }
}
main .main__creator--popup p {
  text-transform: uppercase;
  letter-spacing: 5px;
  padding: 10px 20px 10px 10px;
  color: var(--lightGrayishBlue);
}
@media (min-width: 71.25em) {
  main .main__creator--popup p {
    padding: 10px;
  }
}
main .main__creator--popup ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  margin-left: 15px;
}
@media (min-width: 71.25em) {
  main .main__creator--popup ul {
    margin-left: 0;
  }
}
main .main__creator--popup ul li {
  padding-right: 20px;
}
@media (min-width: 71.25em) {
  main .main__creator--popup ul li {
    padding-right: 15px;
  }
}
main .main__creator .show {
  visibility: visible;
  opacity: 1;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Manrope);
  background: var(--lightGrayishBlue);
}

:root {
  --veryDarkGrayishBlue: hsl(217, 19%, 35%);
  --DeasaturatedDarkBlue: hsl(214, 17%, 51%);
  --grayishBlue: hsl(212, 23%, 69%);
  --lightGrayishBlue: hsl(210, 46%, 95%);
}

:root {
  --font-Manrope: "Manrope", sans-serif;
}